.Display{
    display: flex;
    background: var(--blue-gray-color);
    padding: 0 var(--large-fs);
    width: 100%;
    border: 1px solid var(--light-blue-purple-color);
    border-bottom: none;
}

.result{
    width: 100%;
    margin: auto 0 var(--small-fs) auto;
    display: block;
    font-size: var(--large-fs);
    overflow: hidden;
    text-align: right;
    text-overflow: ellipsis;
}
